.card-container{
    display: flex;
    flex-direction: column;
    background-color: #2da393;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-in-out;    
}

.card-container:hover{
    transform: scale(1.03);
}